//Programs-choose

// -Подбор программы- //

//Описание формы
.programs-choose__disclaimer {
  padding: 5px 0 25px 0;
  text-transform: uppercase;

  @media (min-width: $tablet-width) {
    margin: 0 0 41px 0;

    @include cost-tablet-desktop;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 41px -2px;
  }
}

//Обертка для "подбора программы"
.programs-choose-wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
  }
}

//Обертка для полей ввода данных
.programs-choose__field-wrapper {
  @media (min-width: $desktop-width) {
    width: 589px;
  }
}

//Обертка для конкретного поля ввода данных
.programs-choose__field {
  display: flex;
  margin: 0 0 14px 0;

  @media (min-width: $tablet-width) {
    margin: 0 0 20px 0;
  }
}

//Название поля для ввода
.programs-choose__title {
  padding: 14px 0 0 0;

  @include label;
  color: $special-dark-grey;

  @media (min-width: $tablet-width) {
    padding: 17px 0 0 0;

    @include title-tablet-desktop-auto;
  }
}

//Поле для ввода
.programs-choose__input {
  flex-grow: 1;
  margin: 0 20px 0 10px;
  padding: 13px 15px 12px 15px;
  font-family: $oswald;

  @include label;
  @include border;

  @media (min-width: $tablet-width) {
    margin: 0 20px 0 20px;
    padding: 10px 15px 10px 15px;

    @include title-tablet-desktop-auto;
  }
}

//Обертка фильтра программ
.programs-choose__radio-wrapper {
  width: 305px;
  margin: 23px 0 0 -20px;
  padding: 20px 0 24px 14px;
  border: 1px solid $dark-grey;
  border-left: none;
  border-right: none;

  @media (min-width: $tablet-width) {
    width: 593px;
    margin: 72px 0 0 0;
    padding: 26px 0 26px 35px;
    border: 1px solid $low-grey;
  }

  @media (min-width: $desktop-width) {
    width: 532px;
    margin: 0 0 20px 60px;
  }
}

//Список программ
.programs-choose__list {
  margin: 8px 0 -25px 10px;

  @include label;
  color: $light-black;

  @media (min-width: $tablet-width) {
    @include title-tablet-desktop-auto;
  }
}

//Зоголовок конкретного пункта
.programs-choose__item {
  & .programs-choose__label {
    position: relative;
    display: block;
    padding: 1px 0 24px 1px;
    cursor: pointer;
  }

  @media (min-width: $tablet-width) {
    padding: 1px 0 9px 0;
  }
}

//Радио-кнопка
.programs-choose__radio {
  margin: 15px 25px 15px 5px;
  border: none;

  &:disabled + .programs-choose__label {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    margin: 21px 0 17px 25px;
  }
}

//Стилиация радио кнопки
.programs-choose__radio-filter {
  & + .programs-choose__label {
    //Добавляет круглую серую рамку
    &::before {
      content: "";
      position: absolute;
      top: -5px;
      left: -44px;
      width: 27px;
      height: 27px;
      border: 1px solid $less-gray;
      border-radius: 50%;

      @media (min-width: $tablet-width) {
        top: -11px;
        left: -58px;
        width: 38px;
        height: 38px;
      }
    }

    &:hover {
      &::before {
        border: 1px solid $least-gray;
      }
    }
  }

  &:checked {
    & + .programs-choose__label {
      //Добавляет зеленыю точку
      &::after {
        content: "";
        position: absolute;
        top: 4px;
        left: -35px;
        width: 12px;
        height: 12px;
        background-color: $green;
        border-radius: 50%;

        @media (min-width: $tablet-width) {
          top: -1px;
          left: -48px;
          width: 20px;
          height: 20px;
        }
      }
    }
  }
}

// -Подбор программы- //
